.product {
  // .product__title
  &__title {
    margin-bottom: 15px;
    font-family: UniSansCaps;
    font-size: 36px;
    font-weight: 900;
    line-height: 36px;
    text-align: left;
    color: #333;

    span {
      display: block;
      font-size: 30px;
      color: #dadada;
    }
  }
  // .product__main-panel
  &__main-panel {
    margin-bottom: 40px;
  }
  // .product__description
  &__description {
  }
}

.content-main {
  // .content-main__product
  &__product {
  }
}

.main-panel-product {
  @media (min-width: 768px) {
    display: flex;
  }
  // .main-panel-product__images
  &__images {
    width: 42.434988%;
    min-width: 0;
    margin-right: 3.1%;

    @media (max-width: $md3) {
      width: 80%;
      margin-bottom: 25px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  // .main-panel-product__main-info
  &__main-info {
    flex: 1 1 0;
  }
}

.images-product {
  // .images-product__main
  &__main {
    margin-bottom: 13px;
  }
  // .images-product__thumb
  &__thumb {

  }
}
.images-product-main {
  padding: 10px;
  border: solid 1px #f5f5f5;
  // .images-product-main__slide
  &__slide {
    position: relative;
    height: 0;
    padding-bottom: 85.793871%;
  }
  // .images-product-main__discount
  &__discount {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 6px;
    left: 10px;
    width: 58px;
    height: 58px;
    background-color: #f65738;
    border-radius: 50%;
    font-size: 16px;
    font-weight: 900;
    line-height: 25px;
    letter-spacing: 0.4px;
    text-align: center;
    color: #fff;
  }
  // .images-product-main__img
  &__img {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    object-fit: contain;
  }
}
.images-product-thumb {
  // .images-product-thumb__slide
  &__slide {
    position: relative;
    padding-bottom: 21.86495176%;
    border: solid 1px #e4e4e4;
    &.swiper-slide-thumb-active::after {
      content: '';
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      border: 4px solid #f68038;
    }
  }
  // .images-product-thumb__img
  &__img {
    position: absolute;
    height: 95%;
    width: 95%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: contain;
  }
}


.main-info-product {
  padding-top: 7px;

  @media (max-width: $md3) {
    padding: 0 10px;
  }
  // .main-info-product__top
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
  }
  // .main-info-product__compare
  &__compare {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 33px;
    padding: 0 20px 0 43px;
    font-size: 14px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #666;
    background: #f3f3f3 url(../img/icons/product/compare.png) 13px 6px / auto no-repeat;
    border-radius: 15px;
  }
  // .main-info-product__stock
  &__stock {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 21px;
    padding: 0 10px;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    text-align: left;
    color: #fff;
    background-color: #f68038;
    border-radius: 10px;
  }
  // .main-info-product__actions
  &__actions {
    margin-bottom: 20px;
  }
  // .main-info-product__includes
  &__includes {
  }
}

.actions-main-product {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ededed;
  border-top: 1px solid #ededed;

  @media (max-width: 1160px) {
    flex-wrap: wrap;
  }
  // .actions-main-product__price
  &__price {
    margin-right: 10px;
  }
  // .actions-main-product__old-price
  &__old-price {
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    text-align: left;
    color: #999;
    text-decoration: line-through;
    margin-bottom: 5px;
  }
  // .actions-main-product__current-price
  &__current-price {
    font-size: 24px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #333;
  }
  // .actions-main-product__quantity
  &__quantity {
  }
  // .actions-main-product__cart
  &__cart {
    position: relative;
    display: flex;
    align-items: center;
    height: 34px;
    padding: 0 68px 0 16px;
    font-size: 14px;
    font-weight: 900;
    line-height: 16px;
    text-align: center;
    color: #999;
    background-color: #f3f3f3;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      height: 57px;
      width: 57px;
      background: url(../img/icons/product-item/hover-cart.png) center center/auto no-repeat;
    }

    @media (max-width: 1160px) {
      flex: 1 1 100%;
      margin-top: 25px;
    }
  }
}

.quantity {
  display: flex;
  align-items: center;
  // .quantity__btn
  &__btn {
    width: 16px;
    height: 13px;
    background: url(../img/icons/product-slider/slider-arrow.png) 0 0/ cover no-repeat;
    // .quantity__btn--minus
    &--minus {
      transform: rotate(180deg);
    }
    // .quantity__btn--plus
    &--plus {
    }
  }
  // .quantity__input
  &__input {
    width: 56px;
    height: 33px;
    padding: 5px;
    margin: 0 13px;
    border: solid 2px #e5e5e5;
    background-color: #fff;
    text-align: center;
    font-size: 16px;
    font-weight: 900;
    text-align: center;
    color: #999;
    line-height: 33px;
  }
}

.includes-product {
  display: flex;
  flex-wrap: wrap;

  // .includes-product__title
  &__title {
    flex: 1 1 100%;
    margin-bottom: 25px;
    font-size: 14px;
    font-weight: 900;
    line-height: 18px;
    text-align: left;
    color: #333;
  }
  // .includes-product__list
  &__list {
    display: flex;
    justify-content: space-between;
    min-width: 360px;

    @media (max-width: $md3) {
      min-width: 0;
      justify-content: flex-start;
    }
  }
  // .includes-product__item
  &__item {
    display: flex;
    flex-direction: column;
    padding: 0 14px;

    @media (max-width: $md3) {
      margin-right: 5px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
  // .includes-product__icon
  &__icon {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  // .includes-product__img
  &__img {
    display: block;
    margin: 0 auto;
  }
  // .includes-product__text
  &__text {
    font-size: 12px;
    font-weight: 300;
    line-height: 16px;
    text-align: center;
    color: #999;
  }
}


.description-product {
  // .description-product__nav
  &__nav {
    display: flex;
  }
  // .description-product__item
  &__item {
    height: 49px;
    padding: 0 30px;
    font-size: 14px;
    font-weight: 900;
    line-height: 49px;
    color: #72c9d4;
    text-decoration: underline dashed;
    cursor: pointer;
    user-select: none;

    &.active {
      color: #fff;
      text-decoration: none;
      background-color: #72c9d4;
    }
  }

  // .description-product__body
  &__body {
    padding: 33px;
    margin-bottom: 25px;
    font-size: 14px;
    font-weight: 300;
    line-height: 22px;
    text-align: left;
    color: #666;
    background-color: #fafafa;

    & > * {
      display: none;

      &.active {
        display: block;
      }
    }
  }

  // .description-product__text
  &__text {
  }
  // .description-product__spec
  &__spec {
  }
}

.similar-products {
  // .similar-products__list
  &__list {
    display: flex;
    margin: 0 -15px;
    margin-bottom: 25px;

    @media (max-width: $md3) {
      flex-wrap: wrap;
    }
  }
  // .similar-products__item
  &__item {
    padding: 0 15px;
    flex: 1 1 33.333333%;

    @media (max-width: $md3) {
      flex: 1 1 100%;
    }
  }
}







