.product-item {
  position: relative;
  min-height: 100%;
  padding: 10px 10px 15px 10px;
  display: flex;
  flex-direction: column;
  border: solid 1px #f5f5f5;

  &--submenu {
    border: none;
  }
  // .product-item__labels
  &__labels {
    position: absolute;
    top: 10px;
    left: 10px;
    pointer-events: none;
  }
  // .product-item__label
  &__label {
    padding: 0 15px;
    font-size: 14px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #fff;
    text-transform: uppercase;
    background-color: #fd8424;
    border-radius: 15px;
    margin-bottom: 5px;
  }
  // .product-item__image-link
  &__image-link {
    display: block;
    height: 240px;
    cursor: pointer;

    &--submenu {
      height: 147px;
    }
    & img {
      display: block;
      height: 100%;
      margin: 0 auto;
      object-fit: contain;
    }
  }
  // .product-item__title
  &__title {
    padding: 0 15px;
    margin-bottom: 15px;
    flex: 1 0 auto;

    &:hover {
      text-decoration: underline;
    }
  }
  // .product-item__title-link
  &__title-link {
    display: block;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    text-align: left;
    color: #36949f;
    cursor: pointer;
  }
  // .product-item__category
  &__category {
  }
  // .product-item__name
  &__name {
    font-weight: 900;

    .product-item__title--submenu & {
      font-weight: 300;
    }
  }
  // .product-item__bottom
  &__bottom {
    position: relative;
    padding: 0 15px;
    padding-top: 17px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  // .product-item__old-price
  &__old-price {
    position: absolute;
    right: 15px;
    top: 0;
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    text-align: right;
    color: #999;
    text-decoration: line-through;
  }
  // .product-item__add-to-cart
  &__add-to-cart {
    width: 40px;
    height: 40px;
    background:  url(../img/product-item/add-to-cart-icon.png) 0 0 / cover no-repeat;
  }
  // .product-item__price
  &__price {
    font-size: 24px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #333;
  }
}

.hover-product-item {
  display: flex;
  flex-direction: column;
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 20px;
  padding-top: 15px;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.836);
  border: solid 5px #f68038;
  overflow-y: auto;
  overflow-x: hidden;
  transform: perspective(400px) rotateY(180deg);
  transition: all 0.3s ease 0s;
  backface-visibility: hidden;

  @media (hover: hover) {
    .product-item:hover & {
      transform: perspective(400px) rotateY(0deg);
    }
  }
  // .hover-product-item__title-link
  &__title-link {
    display: block;
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: 900;
    line-height: 22px;
    text-align: left;
    color: #fff;

    &:hover {
      text-decoration: underline;
    }
  }
  // .hover-product-item__name
  &__name {
  }
  // .hover-product-item__category
  &__category {
    font-weight: 300;
    color: #c2c2c2;
  }
  // .hover-product-item__characteristics
  &__characteristics {
    flex: 1 0 auto;
  }
  // .hover-product-item__char-item
  &__char-item {
    margin-bottom: 0px;
    font-size: 14px;
    line-height: 24px;
    text-align: left;
    color: #fff;
  }
  // .hover-product-item__property
  &__property {
  }
  // .hover-product-item__value
  &__value {
    font-weight: 900;
    line-height: 18px;
    color: #f68038;
  }

  &__compare {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #fff;
    text-decoration: underline;
    text-decoration-style: dashed;
    margin-bottom: 50px;

    &:hover {
      text-decoration: none;
    }

    &::before {
      content: '';
      display: inline-block;
      margin-right: 10px;
      margin-left: 5px;
      width: 21px;
      height: 21px;
      background: url(../img/icons/catalog-filter/compare.png) center/auto no-repeat;
    }
  }
  // .hover-product-item__cart
  &__cart {
    flex-shrink: 0;
    height: 57px;
    margin-bottom: 15px;
    background: url(../img/icons/product-item/hover-cart.png) center center / auto no-repeat;

    &--catalog {
      width: 57px;
      height: 57px;
      position: absolute;
      margin-bottom: 0;
      bottom: 18px;
      left: 27px;
    }
  }
  // .hover-product-item__bottom
  &__bottom {
    position: relative;
    padding-top: 17px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &--catalog {
      justify-content: flex-end;
    }
  }
  // .hover-product-item__old-price
  &__old-price {
    position: absolute;
    right: 0;
    top: -10px;
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    text-align: right;
    color: #999;
    text-decoration: line-through;
  }
  // .hover-product-item__stock
  &__stock {
    padding: 0 10px;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    text-align: left;
    color: #f68038;
    background-color: #fff;
    border-radius: 15px;
  }
  // .hover-product-item__price
  &__price {
    font-size: 24px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #fff;
  }
}

