// catalog search

.catalog-search {
  position: relative;
  display: flex;

  @media (max-width: 768px) {
    padding: 0 10px;
  }
  // .catalog-search__select
  &__select {
    background-color: #f3f3f3;

    @media (max-width: $md2) {
      display: none;
    }
  }
  // .catalog-search__select-btn
  &__select-btn {
    display: flex;
    align-items: center;
    height: 39px;
    padding: 0 13px;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    letter-spacing: 0.7px;
    text-align: left;
    color: #666;

    &::before {
      content: '';
      width: 17px;
      height: 17px;
      background:  url(../img/icons/catalog-search/magnifying-glass.png) 0 0 / auto no-repeat;
    }

    &::after {
      content: '';
      width: 10px;
      height: 10px;
      background:  url(../img/icons/catalog-search/catalog-search-arrow.png) 0 0 / auto no-repeat;
      transition: all 0.3s ease 0s;
    }
    span {
      margin: 0 13px;
      border-bottom: 1px dashed #999;
      &:last-of-type {
        display: none;
      }
    }

    &.selected span {
      display: none;
      &:last-of-type {
        display: inline;
      }
    }

    &:hover {
      span {
        border-color: transparent;
      }
    }

    &.active::after {
      transform: rotate(180deg);
    }
  }
  // .catalog-search__categories
  &__categories {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    z-index: 2;
  }
  // .catalog-search__search-input
  &__search-input {
    width: 0;
    flex: 1;

    input {
      font-size: 14px;
      font-weight: 300;
      text-align: left;
      color: #666;
      line-height: 35px;
      width: 100%;
      height: 100%;
      padding: 0 10px;
      border-top: solid 2px #f2f2f2;
      border-bottom: solid 2px #f2f2f2;
      outline: none;

      &:focus {
        border-bottom-color: #f68038;
      }
    }
  }
  // .catalog-search__submit
  &__submit {
  }
}
.categories-search {
  display: flex;
  border: solid 1px #f3f3f3;
  border-bottom: 4px solid #f68038;;
  background-color: #fff;
  // .categories-search__column
  &__column {
    flex: 1 1 33.33333333%;
  }
  // .categories-search__list
  &__list {
    padding: 20px 40px;
    border-right: solid 1px #ddd;
  }
  // .categories-search__item
  &__item {
  }
  // .categories-search__label
  &__label {
    user-select: none;
    cursor: pointer;
  }
  // .categories-search__text
  &__text {
    font-size: 12px;
    font-weight: 300;
    line-height: 36px;
    letter-spacing: 0.3px;
    text-align: left;
    color: #999;

    .categories-search__label input:checked + & {
      color: #f68038;
    }
  }
}

// promo main

.promo-main {
  // .promo-main__slider
  &__slider {
  }
  // .promo-main__wrapper
  &__wrapper {
  }
  // .promo-main__slide
  &__slide {
    position: relative;
    padding: 15px 35px;
    padding-bottom: 60px;
  }
  // .promo-main__content
  &__content {
  }
  // .promo-main__img
  &__img {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: -1;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  // .promo-main__pagination
  & &__pagination {
    position: relative;
    z-index: 2;
    bottom: 0;
    display: flex;
    justify-content: center;
    margin-top: -27.5px;
    counter-reset: bullet;


    & .swiper-pagination-bullet {
      position: relative;
      width: 55px;
      height: 55px;
      margin-right: 45px;
      border: solid 4px #fff;
      border-radius: 50%;
      opacity: 1;
      overflow: hidden;
      counter-increment: bullet;
      background-size: cover;
      background-repeat: no-repeat;

      &:last-child {
        margin-right: 0;
      }

      &::before {
        content: counter(bullet);
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #f68038;
        font-size: 16px;
        font-weight: 900;
        line-height: 24px;
        text-align: left;
        color: #fffefe;
        opacity: 0;
        transition: all 0.3s ease 0s;
      }
    }

    & .swiper-pagination-bullet-active {
      &::before {
        opacity: 1;
      }
    }
  }
}

