//  about-company

.about-company {
  margin-bottom: 38px;
  &__container {
    padding: 40px 57px 55px 37px;
    background: url(../img/about-company/about-company-bg.jpg) 0 0 / cover no-repeat;

    @media (max-width: $md3) {
      padding: 30px 10px 30px 10px;
    }
  }
  // .about-company__title
  &__title {
    margin-bottom: 25px;
    font-family: 'UniSansCaps';
    font-size: 30px;
    font-weight: 900;
    letter-spacing: 0.75px;
    text-align: left;
    color: #22b7ca;
    line-height: 1;
    text-transform: uppercase;
  }
  // .about-company__row
  &__row {
    display: flex;
    justify-content: space-between;

    @media (max-width: $md2) {
      display: block;
    }
  }
  // .about-company__column
  &__column {
    position: relative;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.3px;
    text-align: left;
    color: #fff;

    p {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    @media (max-width: $md2) {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    @media (min-width: 992px) {
      width: calc(507/1046 * 100%);

      &:last-child {
        width: calc(478/1046 * 100%);
      }
    }
  }
  // .about-company__inner
  &__inner {
    position: relative;
    @media (max-width: $md2) {
      height: 100px;
      overflow: hidden;
      transition: height 0.3s ease 0s;

      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 20px;
        background-image: linear-gradient(to top,#009db0, transparent);
      }

      &.active::before {
        display: none;
      }

      &.active {
        height: auto;
        transition: height 0.3s ease 0s;
      }
    }
  }
  // .about-company__label
  &__label {
    font-weight: 900;
    margin-bottom: 5px;
  }
  // .about-company__list
  &__list {
  }
  // .about-company__item
  &__item {
    &::before {
      content: '';
      display: inline-block;
      margin-left: 7px;
      margin-right: 15px;
      width: 11px;
      height: 11px;
      background:  url(../img/icons/about-company/arrow.png) 0 0 / cover no-repeat;
    }
  }
  // .about-company__img-wrapper
  &__img-wrapper {
    display: none;
    float: right;
    height: 110%;
    width: 200px;
    shape-outside: polygon(-16px 99.13%, 1.75% 80.79%, 88.89% 60.70%, 100.5% 99.59%);
  }
  // .about-company__img
  &__img {

    position: absolute;
    bottom: -285px;
    right: -150px;
    height: auto;
  }

  // .about-company__expand-btn
  &__expand-btn {
    display: none;
    margin-top: 10px;
    font-size: 18px;

    @media (max-width: $md2) {
      display: block;
    }
  }
}

.info-menu {
  padding: 20px;
  border-top: 3px dashed #dadada;
  border-bottom: 3px dashed #dadada;
  // .info-menu__inner
  &__inner {
    @media (max-width: $md3) {
      display: flex;
    }

    @media (min-width: 768px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }
  // .info-menu__row
  &__row {
    @media (max-width: $md3) {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }
    }

    @media (min-width: 768px) {
      display: flex;
      padding-bottom: 13px;
      margin-bottom: 13px;
      border-bottom: 1px solid #b0b0b0;

      &:last-child {
        border-bottom: 0;
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
  // .info-menu__label
  &__label {
    margin: 0 34px 0 16px;
    font-size: 14px;
    font-weight: 900;
    letter-spacing: 0.35px;
    text-align: left;
    color: #333;
    line-height: 24px;

    @media (max-width: $md3) {
      margin: 0;
    }
  }
  // .info-menu__list
  &__list {
    @media (min-width: 768px) {
      display: flex;
    }
  }
  // .info-menu__item
  &__item {
    @media (min-width: 768px) {
      margin-right: 34px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  // .info-menu__link
  &__link {
    font-size: 12px;
    font-weight: 300;
    line-height: 24px;
    text-align: left;
    color: #1ab9ce;

   @media (hover: hover) {
    &:hover {
      color: #333333;
    }
   }
  }
}

@supports (shape-outside: polygon(1px 1px)) {
  @media (min-width: 992px) {
    .about-company__img-wrapper {
      display: block;
    }
  }
}










