.footer {
    // .footer__container
  &__container {
    @media (max-width: $md3) {
      padding: 0 10px;
    }
  }
  // .footer__body
  &__body {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;

    @media (max-width: $md2) {
      display: block;
    }
  }
  // .footer__column
  &__column {
    width: 47.5%;

    @media (max-width: $md2) {
      width: auto;
      margin: 0 auto;

      &:first-child {
        margin-bottom: 25px;
      }
    }
  }
  // .footer__block
  &__block {
    &:first-child {
      margin-bottom: 40px;
    }
  }
  // .footer__widgets
  &__widgets {
    display: flex;
    justify-content: space-between;
  }
  // .footer__bottom
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-top: 1px solid #dadada;
  }
  // .footer__copy
  &__copy {
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #999;
  }
  // .footer__social
  &__social {
  }
}

.block-footer {
  &:first-child {
    display: flex;

    @media (max-width: 580px) {
      display: block;
    }
  }
  // .block-footer__column
  &__column {
    &:first-child {
      width: 163px;
      margin-right: 7%;

      @media (max-width: 580px) {
        margin: 0 auto;
        margin-bottom: 40px;
      }
    }

    &:last-child {
      flex: 1 0 0;
    }
  }
  // .block-footer__logo
  &__logo {
    margin-bottom: 25px;
  }
  // .block-footer__phone
  &__phone {
  }
  // .block-footer__payment
  &__payment {
  }
}
.footer-logo {
  display: block;
  // .footer-logo__img
  &__img {
    display: block;
  }
}
.phone-footer {
  padding-left: 40px;
  background:  url(../img/icons/footer/footer-phone.png) 0 center / auto no-repeat;
  // .phone-footer__link
  &__link {
    display: block;
    font-size: 14px;
    font-weight: 900;
    line-height: 18px;
    text-align: left;
    color: #333;
    text-transform: uppercase;
  }
}

.delivery-footer {
  overflow: hidden;
  // .delivery-footer__title
  &__title {
    margin-bottom: 15px;
    font-size: 14px;
    font-weight: 900;
    letter-spacing: 0.35px;
    text-align: left;
    line-height: 24px;
    color: #999;
    text-transform: uppercase;

    @media (max-width: 580px) {
      text-align: center;
    }
  }
  // .delivery-footer__list
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -13px;
    justify-content: center;
    align-items: center;
  }
  // .delivery-footer__item
  &__item {
    max-width: 50%;
    max-height: 100px;
    padding: 0 15px;
    margin-bottom: 20px;
  }
  // .delivery-footer__link
  &__link {
  }
  // .delivery-footer__img
  &__img {

  }
}
.payment-footer {
  // .payment-footer__img
  &__img {
    display: block;

    @media (max-width: 580px) {
      margin: 0 auto;
    }
  }
}
.widgets-footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: $md2) {
    justify-content: flex-start;
  }

  @media (max-width: 580px) {
      display: block;
  }
  // .widgets-footer__widget
  &__widget {
    width: 47.5%;

    @media (max-width: $md2) {
      width: auto;
      margin-right: 15px;
      &:last-child {
        margin-right: 0;
      }
    }

    @media (max-width: 580px) {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  // .widgets-footer__img
  &__img {
    display: block;
    margin: 0 auto;
    @media (max-width: 580px) {
      margin: 0 auto;
    }
  }
}

.social-footer {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  // .social-footer__item
  &__item {
    margin-right: 15px;
    &:last-child {
      margin-right: 0;
    }
  }
  // .social-footer__link
  &__link {
    display: block;
  }
  // .social-footer__img
  &__img {
    width: 30px;
    height: 30px;
    display: block;
  }
}
