// <SELECT>
// Снипет (HTML): sel
.select {
	position: relative;
	// .select__body
	&__body {

    width: 166px;
		position: relative;
	}
	// .select__title
	&__title {
		color: inherit;
		text-align: left;
		background-color: #fff;
		cursor: pointer;
		width: 100%;
	}
	// .select__value
	&__value {
    white-space: nowrap;
    height: 29px;
    line-height: 29px;
    padding: 0 25px 0 16px;
		display: flex;
		align-items: center;

		&:after {

			content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 6px;
      width: 14px;
      height: 8px;
      background:  url(../img/icons/catalog/catalog-arrow-sort.png) center / auto no-repeat;
			._select-open & {
				transform: translateY(-50%) rotate(-180deg);
			}
		}
		&._select-pseudo-label {
			&::before {
				content: attr(data-pseudo-label);
				opacity: 0.5;
			}
		}
	}

  &__content {
    overflow-x: hidden;
    text-overflow: ellipsis;
  }
	// .select__text
	&__text {
		flex: 1 1 auto;
	}
	&__input {
		width: 100%;
		background-color: transparent;
		height: 100%;
	}
	// .select__options
	&__options {
		position: absolute;
		top: 100%;
		min-width: 100%;
		left: 0;
    color: #333;
		background-color: #fff;
		padding: 10px 0px 5px 0px;
	}
	// .select__scroll
	&__scroll {
		overflow-y: auto;
		overflow-x: hidden;
		// Максимальная высота
		max-height: 200px;
		// Стили скроллбара
		.simplebar-scrollbar {
			&::before {
				//opacity: 1;
			}
		}
	}
	// .select__option
	&__option {
		width: 100%;
		text-align: left;
		cursor: pointer;
		padding: 5px 15px;
		color: inherit;
    white-space: nowrap;
		&._select-selected {
			background-color: #eee;
		}
		@media (any-hover: hover) {
			&:hover {
				background: #d9d9d9;
			}
		}
	}

	// Конструкция дополнительных данных
	&__row {
		display: inline-flex;
		align-items: center;
	}
	&__asset {
	}
	&__text {
	}

	// Состояния селекта
	&._select-open {
		z-index: 5;
	}
	&._select-disabled {
	}
	&._select-active {
	}
	&._select-focus {
	}
	&._select-multiple {
	}
	&._select-checkbox {
	}
}

// Тег
._select-tag {
	cursor: pointer;
}
