.checkout {
  margin-bottom: 30px;
  // .checkout__title
  &__title {
    margin-bottom: 20px;
    font-family: 'UniSansCaps';
    font-size: 36px;
    font-weight: 900;
    line-height: 22px;
    text-align: left;
    color: #dadada;
    text-transform: uppercase;

    @media (max-width: $md3) {
      padding: 0 10px;
    }
  }
  // .checkout__content
  &__content {
    margin-bottom: 50px;
  }
  // .checkout__order
  &__order {
  }
}

.content-checkout {
  padding: 20px 30px 35px 45px;
  background-color: #fafafa;

  @media (max-width: $md3) {
    padding: 10px;
  }
  // .content-checkout__nav
  &__nav {
    display: flex;
    margin-bottom: 30px;

    @media (max-width: $md3) {
      flex-wrap: wrap;
      margin-bottom: 10px;
    }
  }
  // .content-checkout__btn
  &__btn {
    height: 49px;
    padding: 0 25px;
    font-size: 14px;
    font-weight: 900;
    line-height: 16px;
    text-align: left;
    color: #72c9d4;
    text-transform: uppercase;
    text-decoration: underline dashed;

    &.tabs__btn_active {
      background-color: #72c9d4;
      color: #fff;
      text-decoration: none;
    }

    @media (max-width: $md3) {
      flex: 1 1 100%;
      margin-bottom: 15px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  // .content-checkout__content
  &__content {
    margin-bottom: 30px;
  }
  // .content-checkout__pane
  &__pane {
    display: none;

    &.tabs__pane_show {
      display: block;
    }
  }
  // .content-checkout__form
  &__form {
  }
}
.tabs {
  // .tabs__btn
  &__btn {
  }
  // .tabs__btn_active
  &__btn_active {
  }
  // .tabs__pane
  &__pane {
  }
  // .tabs__pane_show
  &__pane_show {
  }
}
.form-checkout {
  @media (min-width: 768px) {
    display: flex;
  }
  // .form-checkout__main
  &__main {
    flex: 1;
    margin-right: 45px;

    @media (max-width: $md3) {
      margin-bottom: 25px;
      margin-right: 0;
    }
  }
  // .form-checkout__line
  &__line {
    display: flex;
    margin-bottom: 9px;

    @media (max-width: $md4) {
      flex-wrap: wrap;
    }
  }
  // .form-checkout__label
  &__label {
    width: 120px;
    font-size: 14px;
    font-weight: 900;
    line-height: 40px;
    text-align: left;
    color: #333;

    span {
      color: #f68038;
      font-weight: 300;
    }

    @media (max-width: $md4) {
      width: auto;
    }
  }
  // .form-checkout__input
  &__input {
    margin-left: 15px;
    flex: 1;

    @media (max-width: $md4) {
      flex: 1 1 100%;
      margin-left: 0;
    }

    input {
      width: 100%;
      display: block;
      height: 31px;
      padding: 0 10px;
      font-size: 14px;
      font-weight: 300;
      text-align: left;
      color: #333;
      line-height: 31px;
      background-color: #fff;
      border: solid 1px #e5e5e5;
      outline: none;

      &:focus {
        border-color: #f68038;
      }
    }
  }
  // .form-checkout__bottom
  &__bottom {
    margin-left: 135px;

    @media (max-width: $md4) {
      margin-left: 0;
      margin-top: 25px;
    }
  }
  // .form-checkout__text
  &__text {
    width: 40%;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #666;

    @media (max-width: $md3) {
      width: auto;
    }
  }
  // .form-checkout__required
  &__required {
    display: block;
    margin-bottom: 35px;

    span {
      font-size: 14px;
      font-weight: 900;
      line-height: 40px;
      color: #f68038;
    }

    @media (max-width: $md3) {
      margin-bottom: 15px;
    }
  }
}
.subscribe-checkout {
  // .subscribe-checkout__label
  &__label {
  }
  // .subscribe-checkout__text
  &__text {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #666;
    user-select: none;

    &::before {
      content: '';
      width: 15px;
      height: 15px;
      padding: 3px;
      margin-right: 15px;
      background-color: #fff;
      border: 1px solid #e5e5e5;
    }

    input:checked + &::before {
      background-color: #f68038;
      background-clip: content-box;
    }
  }
}


.order-checkout {
  // .order-checkout__title
  &__title {
    margin-bottom: 25px;
    font-family: 'UniSansCaps';
    font-size: 36px;
    font-weight: 900;
    line-height: 22px;
    text-align: left;
    color: #dadada;
    text-transform: uppercase;

    @media (max-width: $md3) {
      padding: 0 10px;
    }
  }
  // .order-checkout__items
  &__items {
    margin-bottom: 30px;
  }
  // .order-checkout__item
  &__item {
    margin-bottom: 11px;
  }
  // .order-checkout__bottom
  &__bottom {
    text-align: right;
    margin-bottom: 20px;
    @media (max-width: $md3) {
      padding: 0 10px;
    }
  }
  // .order-checkout__total
  &__total {
    margin-bottom: 25px;
    text-align: right;
    padding: 0 15px;
    font-size: 24px;
    font-weight: 900;
    line-height: 16px;
    color: #333;
    text-transform: uppercase;

    span {
      font-size: 36px;
      font-weight: 900;
      line-height: 24px;
      text-align: left;
      color: #f68038;
    }
  }
  // .order-checkout__submit
  &__submit {
    display: inline-block;
    height: 33px;
    padding: 0 20px;
    font-size: 14px;
    font-weight: 900;
    line-height: 33px;
    text-align: left;
    color: #fff;
    text-transform: uppercase;
    background-color: #f68038;
    border-radius: 15px;
  }
}
.item-checkout {
  display: flex;
  justify-content: space-between;
  padding: 25px 15px 15px 20px;
  border: solid 1px #f5f5f5;

  @media (max-width: $md3) {
    flex-wrap: wrap;
  }
  // .item-checkout__body
  &__body {
    display: flex;

    @media (max-width: $md3) {
      flex: 1 1 100%;
    }
  }
  // .item-checkout__img
  &__img {
    display: block;
    margin-right: 20px;

    img {
      display: block;
      height: 100px;
      width: 110px;
      object-fit: contain;
    }

  }
  // .item-checkout__content
  &__content {
    padding-top: 30px;
  }
  // .item-checkout__title
  &__title {
    max-width: 210px;
    margin-bottom: 10px;
    margin-right: 20px;
    font-size: 14px;
    font-weight: 900;
    line-height: 22px;
    text-align: left;
    color: #36949f;

    span {
      font-weight: 300;
    }
  }
  // .item-checkout__price
  &__price {
    font-size: 24px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #333;
  }
  // .item-checkout__quantity
  &__quantity {
    margin-top: 40px;
    margin-right: 20px;
  }
  // .item-checkout__total
  &__total {
    padding-top: 30px;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    text-align: left;
    color: #999;

    span {
      display: block;
      margin-top: 10px;
    }

    @media (max-width: $md3) {

      span {
        display: inline-block;
        margin-left: 15px;
      }
    }

    @media (max-width: $md4) {
      flex: 1 1 100%;
    }
  }
  // .item-checkout__close
  &__close {
    position: absolute;
  }
}


