.toggle {
  position: absolute;
  z-index: 999;
  width: 40px;
  height: 24px;
  cursor: pointer;

  &::before, &::after, & span {
    content: '';
    position: absolute;
    left: 0;
    width: 40px;
    height: 2px;

    background-color: #fff;
    transition: all 0.2s ease-out 0s;
  }

  & span {
    top: 50%;
    margin-top: -1px;
    opacity: 1;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  .menu--opened & span {
    opacity: 0;
  }

  .menu--opened &::before,  .menu--opened &::after{
    bottom: auto;
    top: 50%;
    margin-top: -1px;
  }

  .menu--opened &::before {
    transform: rotate(45deg);
  }

  .menu--opened &::after {
    transform: rotate(-45deg);
  }
}


