.catalog-menu {
  position: relative;
  z-index: 10;
  background:  url(../img/catalog-menu/catalog-menu-bg.jpg) 0 0 / cover no-repeat;
  // .catalog-menu__header
  &__header {
    position: relative;
    padding: 15px 20px;
  }
  // .catalog-menu__title
  &__title {
    width: 170px;
    font-family: 'UniSans', sans-serif;
    font-size: 30px;
    font-weight: 900;
    letter-spacing: 0.75px;
    text-align: left;
    color: #fff;
    text-transform: uppercase;
    line-height: 30px;
  }
  // .catalog-menu__button
  &__button {
    position: absolute;
    top: 50%;
    right: 20px;
    width: 36px;
    height: 35px;
    transform: translateY(-50%);;
    cursor: pointer;
    background-color: #068292;

    &::before, &::after, & span {
      content: '';
      position: absolute;
      left: 6px;
      width: 24px;
      height: 2px;

      background-color: #fff;
      transition: all 0.2s ease-out 0s;
    }

    & span {
      top: 50%;
      margin-top: -1px;
      opacity: 1;
    }

    &::before {
      top: 10px;
    }

    &::after {
      bottom: 10px;
    }

    .catalog-menu--opened & span {
      opacity: 0;
    }

    .catalog-menu--opened &::before,  .catalog-menu--opened &::after{
      bottom: auto;
      top: 50%;
      margin-top: -1px;
    }

    .catalog-menu--opened &::before {
      transform: rotate(45deg);
    }

    .catalog-menu--opened &::after {
      transform: rotate(-45deg);
    }
  }
  // .catalog-menu__list
  &__list {
    padding-bottom: 10px;
  }
  // .catalog-menu__item
  &__item {
    border-bottom: 1px solid #1aa7b9;
    &:last-child {
      border-bottom: none;
    }

    // .catalog-menu__item--parent
    &--parent {
    }
  }
  // .catalog-menu__link
  &__link {
    display: block;
    position: relative;
    padding-left: 20px;
    padding-right: 30px;
    font-size: 12px;
    font-weight: 300;
    line-height: 40px;
    letter-spacing: 0.3px;
    text-align: left;
    color: #fff;
    cursor: pointer;

    @media (hover: hover) {
      &:hover {
        background-color: #098494;
      }
    }

    .catalog-menu__item--parent &::before {
      content: '';
      position: absolute;
      top: 50%;
      right: 20px;
      height: 14px;
      width: 14px;
      background:  url(../img/icons/catalog-menu/parent-item-arrow.png) 0 0 / cover no-repeat;
      transform: translateY(-50%);

      @media (max-width: $md2) {
        transform: translateY(-50%) rotate(90deg);
      }
    }

    @media (max-width: $md2) {
      .catalog-menu__item--opened &::before {
        transform: translateY(-50%) rotate(270deg);
      }
    }
  }
  // .catalog-menu__submenu
  &__submenu {
    @media (min-width: 992px) {
      position: absolute;
      top: 0;
      left: 100%;
      width: 100%;
      height: 100%;
    }
  }
}

.catalog-submenu {
  background-color: #fff;
  pointer-events: none;

  &__inner {
    pointer-events: auto;
  }

  @media (min-width: 992px) {
    background-color: transparent;
    overflow: hidden;
    // .catalog-submenu__inner
    &__inner {
      background-color: #fff;
      min-height: 100%;
      padding-top: 20px;
      border: solid 2px #098494;
      transform: translateX(-100%);
      transition: all 0.3s ease 0s;

      .catalog-menu__item--opened & {
        transform: translateX(0);
        z-index: 2;
      }
    }
  }
  // .catalog-submenu__menu
  &__menu {
    @media (min-width: 992px) {
      padding-bottom: 20px;
      border-bottom: 1px solid #dedede;
    }
  }
  // .catalog-submenu__item
  &__item {
  }
  // .catalog-submenu__link
  &__link {
    display: block;
    padding: 0 40px;
    font-size: 12px;
    font-weight: 300;
    line-height: 36px;
    letter-spacing: 0.3px;
    text-align: left;
    color: #1ab9ce;

    &:hover {
      color: #fff;
      background-color: #71c7d3;
    }
  }
  // .catalog-submenu__product
  &__product {
    display: none;
    @media (min-width: 992px) {
      display: block;
    }
  }
}

