html, body {
  height: 100%;
}

body {
  min-width: $minWidth;
  max-width: $maxWidth;
  margin: 0 auto;

  font-size: 12px;
  line-height: 20px;
  font-family: $font-stack-default;
  color: $font-color;
  font-weight: normal;
  font-style: normal;

  background-color: $bg-color;

  .lock & {
    overflow-y: hidden;
  }
}

[hidden] {
  display: none !important;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

.rub::after {
  content: '₽';
}

.wrapper {
  min-height: 100%;
}

.main {
  overflow: hidden;
  margin-bottom: 30px;
  @media (max-width: $md3) {
    margin-top: 30px;
  }
  &__container {
    @media (min-width: 992px) {
      display: flex;
    }
  }
  &__aside {
    margin-bottom: 20px;
    @media (min-width: 992px) {
      width: 260px;
      margin-bottom: 40px;
    }
  }
  &__content {

    @media (min-width: 992px) {
      margin-left: 30px;
      flex: 1;
      // swiper incorrect behaviour fix
      min-width: 0;
    }
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number]{
  -moz-appearance: textfield;
}

