.btn {
  display: inline-flex;
  height: 39px;
  padding: 0 25px;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 900;
  line-height: 18px;
  text-align: left;
  color: #fff;
  text-transform: uppercase;
  background-color: #f68038;

  &:hover {
    background-color: #db7231;
  }
}
