.header {
  &.sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
  }

  &.hide {
    visibility: hidden;
  }

  &.show {
    visibility: visible;
  }
  // .header__top
  &__top {
  }
  // .header__bottom
  &__bottom {
    @media (max-width: $md3) {
      display: none;
    }
  }
}

.top-header {
  position: relative;
  background: url(../img/header/header-top-bg.jpg) center top / cover no-repeat;

  @media (max-width: $md2) {
    background: none;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: 10;
    left: 50%;
    top: 100%;
    width: 193px;
    height: 39px;
    margin-top: -1px;
    background: url(../img/header/header-top-bg-circle.png) 0 0 / cover no-repeat;
    transform: translateX(-50%);

    @media (max-width: $md2) {
      top: 0;
      left: 0;
      z-index: 910;
      width: 100%;
      height: 100%;
      margin-top: 0px;
      background: url(../img/header/header-top-bg.jpg) center top / cover no-repeat;
      transform: none;
    }
  }
  // .top-header__inner
  &__inner {
    position: relative;
    display: flex;
    padding: 14px 0;

    @media (max-width: $md2) {
      height: 66px;
      padding: 0;
      align-items: center;
    }
  }
  // .top-header__logo
  &__logo {
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translateX(-50%);

    @media (max-width: $md2) {
      top: 50%;
      z-index: 930;
      transform: translate(-50%, -50%);
    }
  }
  // .top-header__menu
  &__menu {
    min-width: 0;
    flex: 1 0 0;
    margin-right: 210px;
  }
  // .top-header__contacts
  &__contacts {
    flex: 1 0 0;
    margin-top: 7px;

    @media (max-width: $md2) {
      position: relative;
      z-index: 120;
      margin-top: 0;
    }
  }
}

.logo {
  display: block;
  width: 128px;
  cursor: pointer;

  @media (max-width: $md2) {
    width: 100px;
  }

  @media (max-width: 420px) {
    width: 70px;
  }
  // .logo__img
  &__img {
    display: block;
  }
}

.menu {
  // .menu__toggle
  &__toggle {
    display: none;

    @media (max-width: $md2) {
      display: block;
      left: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  // .menu__body
  &__body {
    @media (max-width: $md2) {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 899;
      width: 100%;
      height: 100%;
      padding-top: 86px;
      padding-left: 30px;

      background-color: #ffffff;
      transform: translateY(-100%);
      overflow-y: auto;
      transition: all 0.3s cubic-bezier(0.18, 0.91, 0.79, 1);

      .menu--opened & {
        transform: translateY(0%);
      }
    }

    @media (max-width: $md3) {
      padding-top: 76px;
    }
  }
  // .menu__list
  &__list {
    display: grid;
    grid-template-columns: max-content max-content max-content;
    gap: 6px 5px;

    @media (max-width: $md2) {
      display: block;
    }
  }
  // .menu__item
  &__item {
    display: flex;
    align-items: flex-start;

    @media (max-width: $md2) {
      display: block;
      margin-bottom: 20px;
    }
  }
  // .menu__link
  &__link {
    padding: 5px 20px;
    font-family: 'Play', sans-serif;
    font-size: 14px;
    font-weight: bold;
    line-height: 18px;
    text-align: left;
    color: #fff;
    text-decoration: underline;
    border-radius: 10px;
    cursor: pointer;
    white-space: nowrap;

    @media (max-width: $md1) {
      padding: 5px 10px;
    }

    @media (max-width: $md2) {
      padding: 0;
      color: #000;
      font-size: 23px;
    }

    &:hover {
      background-color: #068292;

      @media (max-width: $md2) {
        background: none;
      }
    }
  }
}

.toggle {
}

.contacts {
  display: flex;
  justify-content: center;

  @media (max-width: $md2) {
    position: relative;
    z-index: 940;
    justify-content: flex-end;
  }
  // .contacts__item
  &__item {
    @media (max-width: $md2) {
      &--free {
        display: none;
      }
    }
  }
  // .contacts__link
  &__link {
    display: block;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
    // .contacts__link--icon
    &--icon {
      padding-left: 38px;

      @media (max-width: $md1) {
        padding-left: 30px;
      }
    }

    &--icon--phone {
      padding-right: 33px;
      margin-right: 29px;
      background:  url(../img/icons/header/top-header-phone.png) left bottom / auto no-repeat;

      &::before {
        content: '';
        position: absolute;
        right: 0;
        top: 50%;
        height: 28px;
        transform: translateY(-50%);
        border-right: 1px solid #fff;
      }

      @media (max-width: $md1) {
        padding-right: 15px;
        margin-right: 15px;
        background-position: left center;
        background-size: auto 23px;
      }

      @media (max-width: $md2) {
        padding-right: 0;
        margin-right: 0;

        &::before {
          display: none;
        }
      }

      @media (max-width: $md3) {
        width: 23px;
        height: 100%;
        padding: 0;
        background-size: auto 35px;
      }

      @media (max-width: 420px) {
        width: 23px;
        height: 100%;
        padding: 0;
        background-size: auto 30px;
      }
    }
  }
  // .contacts__label
  &__label {
    margin-bottom: 2px;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #fff;

    @media (max-width: $md3) {
      display: none;
    }

    .contacts__link:hover & {
      opacity: 0.6;
    }
  }
  // .contacts__phone
  &__phone {
    font-size: 18px;
    font-weight: 900;
    line-height: 18px;
    text-align: left;
    color: #fff;

    @media (max-width: $md3) {
      display: none;
    }

    .contacts__link:hover & {
      opacity: 0.6;
    }
  }
}

.bottom-header {
  // .header-bottom__inner
  &__inner {
    display: flex;
    align-items: center;
    padding: 10px 0;
  }
  // .bottom-header__user-actions
  &__user-actions {
    flex: 1 0 0;
    margin-right: 210px;

    @media (max-width: $md2) {
      margin-right: 0;
    }
  }
  // .bottom-header__header-info
  &__header-info {
    flex: 1 0 0;
  }
}

.user-actions {
  display: flex;
  justify-content: space-between;

  @media (max-width: $md2) {
    justify-content: flex-start;
    padding-left: 20px;
  }

  @media (max-width: $md3) {
    padding: 0;
    margin-bottom: 15px;
  }
  // .user-actions__item
  &__item {
    margin-right: 25px;
  }
  // .user-actions__link
  &__link {
    display: block;
    padding-left: 33px;
    font-size: 14px;
    font-weight: 300;
    line-height: 30px;
    text-align: left;
    color: #999;
    white-space: nowrap;
    background: left center / auto no-repeat;
    cursor: pointer;
    // .user-actions__link--sign-in

    &--sign-in {
      background-image: url('../img/icons/header/sign-in.png');
    }
    // .user-actions__link--sign-up
    &--sign-up {
      background-image: url('../img/icons/header/sign-up.png');
    }
    // .user-actions__link--mail
    &--mail {
      background-image: url('../img/icons/header/mail.png');
    }

    @media (max-width: $md2) {
      padding-left: 0;
      background: none;
    }

    &:hover span {
      border: none;
      color: #1ab9ce;
    }

    & span {
      border-bottom: 1px dotted;
    }
  }
}
.header-info {
  display: flex;
  justify-content: flex-start;

  @media (max-width: $md2) {
    justify-content: flex-end;
  }
  // .header-info__callback
  &__callback {
    display: flex;
    align-items: center;
    padding-left: 32px;
    margin: 0 8% 0 2%;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #1ab9ce;
    background:  url(../../img/icons/header/bottom-header-phone.png) left center / auto no-repeat;

    & span {
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        border-bottom: 1px dotted;
      }
    }

    &:hover span::before{
      border-bottom: none;
    }

    @media (max-width: 1050px) {
      padding-left: 0;
      background: none;
    }

    @media (max-width: $md3) {
      display: none;
    }
  }
  // .header-info__schedule
  &__schedule {
    @media (max-width: 992px) {
      display: none;
    }
  }
  // .header-info__cart
  &__cart {
    margin-left: auto;

    @media (max-width: 992px) {
      margin-left: 30px;
      margin-right: 5px;
    }
  }
}
.schedule {
  padding-left: 32px;
  margin-right: 39px;
  background: url(../img/icons/header/schedule.png) left center / auto no-repeat;
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  text-align: left;
  color: #999;
  // .schedule__item
  &__item {
  }
  // .schedule__day
  &__day {
    font-weight: 900;
  }
  // .schedule__time
  &__time {
  }
}
.cart {
  position: relative;
  width: 40px;
  height: 40px;
  background: url(../img/icons/header/cart.png) 0 0 / cover no-repeat;
  transform: translateX(-80%);
  cursor: pointer;

  @media (max-width: $md3) {
    transform: translateX(0);
  }

  @media (max-width: 420px) {
    margin-left: 20px;
  }
  // .cart__items-number
  &__items-number {
    position: absolute;
    top: 0;
    left: 80%;
    padding: 4px 10px;
    font-size: 18px;
    font-weight: bold;
    line-height: 17px;
    color: #fff;
    background-color: #f68038;
    border-radius: 15px;

    @media (max-width: $md3) {
      top: -10px;
      left: 0;
      transform: translateX(-50%);
    }

    @media (max-width: 420px) {
      padding: 2px 10px;
      transform: translateX(0);
    }
  }
}
