.catalog-filter {
  padding: 20px 0;
  border: solid 4px #72c9d4;
  // .catalog-filter__title
  &__title {
    position: relative;
    padding: 0 18px;
    margin-bottom: 15px;
    font-size: 24px;
    font-weight: 900;
    letter-spacing: 0.6px;
    line-height: 1;
    text-align: left;
    color: #36949f;
    text-transform: uppercase;

    @media (max-width: $md2) {
      margin-bottom: 0;
      &.active {
        margin-bottom: 15px;
        padding-right: 40px;
      }

      &::after {
        content: '';
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        width: 15px;
        height: 15px;
        background:  url(../img/icons/catalog-search/catalog-search-arrow.png) 0 0 / auto no-repeat;
      }
    }
  }
  // .catalog-filter__body
  &__body {
    @media (min-width: 992px) {
      display: block !important;
    }
  }
  // .catalog-filter__section
  &__section {
  }

  // .catalog-filter__bottom
  &__bottom {

    display: flex;
    justify-content: space-between;
    padding: 0 20px;

    @media (max-width: $md2) {
      padding: 0 10px;
    }
  }
  // .catalog-filter__btn
  &__btn {
    height: 35px;
    min-width: 96px;
    padding: 0 12px;
    font-size: 14px;
    line-height: 35px;
    text-align: center;
    color: #fff;
    background-color: #fd8424;
    // .catalog-filter__btn--submit
    &--submit {
    }
    // .catalog-filter__btn--reset
    &--reset {
      color: #666;
      background-color: #e6e6e6;

    }
  }
}

.section-body {
}

.section-filter {
  padding: 0 15px;
  border-bottom: 1px solid #72c9d4;

  &:last-child {
    border-bottom: none;
  }
  // .section-filter__title
  &__title {
    padding: 15px 0;
    font-size: 16px;
    font-weight: 900;
    line-height: 22px;
    text-align: left;
    color: #36949f;

    &[data-spoller] {
      position: relative;
      padding-right: 40px;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 30px;
        height: 30px;
        border: solid 1px #72c9d4;
        border-radius: 50%;
        transform: translateY(-50%) rotate(-90deg);
        transition: all 0.3s ease 0s;
        background: url(../img/icons/catalog-search/catalog-search-arrow.png) center / auto no-repeat;
      }
    }

    &._spoller-active[data-spoller]::before {
      transform: translateY(-50%) rotate(0deg);
    }
  }
  // .section-filter__body
  &__body {
    padding-bottom: 25px;
  }
}
.price-filter {
  // .price-filter__slider
  &__slider {
    height: 4px;
    margin-bottom: 50px;
    border-radius: 0;
    background-color: #eaeaea;
    border: none;

    & .noUi-connect {
      background-color: #ffcda5;
    }

    & .noUi-handle {
      top: -9px;
      right: -11.5px;
      width: 23px;
      height: 23px;
      border: 1px solid #f68038;
      border-radius: 50%;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        top: 3px;
        left: 3px;
        width: 15px;
        height: 15px;
        background-color: #f68038;
        border-radius: 50%;
      }

      &::after {
        display: none;
      }
    }

    & .noUi-base {
      width: calc(100% - 23px);
      margin: 0 auto;
    }

    & .noUi-tooltip {
      top: 100%;
      bottom: initial;
      margin-top: 8px;
      padding: 0;
      font-size: 12px;
      font-weight: 300;
      line-height: 30px;
      text-align: left;
      color: #999;
      border: none;
      background-color: transparent;
    }
  }
  // .price-filter__inputs
  &__inputs {
    display: flex;
  }
  // .price-filter__label
  &__label {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 300;
    line-height: 30px;
    text-align: left;
    color: #999;

    &:first-child {
      margin-right: 35px;
    }

    span {
      margin-right: 7px;
    }
  }
  // .price-filter__input
  &__input {
    width: 62px;
    height: 23px;
    padding: 0 10px;
    font-size: 12px;
    font-weight: 300;
    line-height: 23px;
    text-align: left;
    color: #000;
    border: solid 1px #dadada;
    outline: none;

    &:focus {
      border-color: #72c9d4;
    }
  }
}

.compare-filter {
  // .compare-filter__items
  &__items {
    margin-bottom: 15px;
  }
  // .compare-filter__item
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  // .compare-filter__link
  &__link {
    padding-right: 10px;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
    color: #1ab9ce;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
  // .compare-filter__remove
  &__remove {
    min-width: 10px;
    height: 10px;
    margin-right: 17px;
    background:  url(../img/icons/catalog-filter/remove-item.png) 0 0 / auto no-repeat;
    cursor: pointer;
  }
  // .compare-filter__bottom
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  // .compare-filter__btn
  &__btn {
    height: 33px;
    padding-left: 42px;
    padding-right: 20px;
    font-size: 14px;
    font-weight: 900;
    line-height: 33px;
    text-align: left;
    color: #fff;
    text-transform: uppercase;
    background: #1ab9ce url(../img/icons/catalog-filter/compare.png) 13px center / auto no-repeat;
    border-radius: 15px;

    &:hover {
      background-color: #169fb1;
    }
  }
  // .compare-filter__clear
  &__clear {
    width: 40px;
    height: 40px;
    background: #e6e6e6 url(../img/icons/catalog-filter/trash.png) center / auto no-repeat;
    border-radius: 50%;
  }
}


.compare-checkbox {
  // .compare-checkbox__label
  &__label {
    cursor: pointer;
    user-select: none;
  }
  // .compare-checkbox__text
  &__text {
    display: flex;
    font-size: 12px;
    font-weight: 300;
    line-height: 30px;
    text-align: left;
    color: #666;

    &::before {
      content: '';
      width: 15px;
      height: 15px;
      padding: 2px;
      margin-top: 7px;
      border: solid 2px #dadada;
      margin-right: 12px;
    }

    input:checked + &::before {
      border-color: #53b5de;
      background-color: #53b5de;
      background-clip: content-box;
    }
  }
}

.breadcrumbs {
  margin-bottom: 40px;
  overflow-x: auto;

  // .breadcrumbs__list
  &__list {
    display: flex;
  }
  // .breadcrumbs__item
  &__item {
  }
  // .breadcrumbs__link
  &__link {
    display: inline-block;
    height: 26px;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
    text-align: left;
    color: #999;

    &[href] {
      margin-right: 16px;
      color: #1ab9ce;
      text-decoration: underline;

      &::after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 10px;
        height: 10px;
        margin-left: 16px;
        transform: rotate(-90deg);
        background:  url(../img/icons/catalog-search/catalog-search-arrow.png) 0 0 / auto no-repeat;
      }

      &:hover {
        text-decoration: none;
      }
    }

    white-space: nowrap;
  }

  @media (max-width: $md3) {
    padding: 0 10px;
  }
}
.content-main {
  // .content-main__breadcrumbs
  &__breadcrumbs {
    @media (max-width: $md3) {
      margin-bottom: 20px;
    }
  }
}

.catalog {
  // .catalog__title
  &__title {
    margin-bottom: 22px;
    font-family: 'UniSansCaps';
    font-size: 30px;
    font-weight: 900;
    line-height: 1;
    letter-spacing: 0.75px;
    text-align: left;
    color: #dadada;
    text-transform: uppercase;
  }
  // .catalog__actions
  &__actions {
  }
  // .catalog__nav
  &__nav {
  }
  // .catalog__products
  &__products {
  }
}
.actions-catalog {
  padding: 10px 17px 10px 23px;
  margin-bottom: 15px;
  background-color: #f3f3f3;
  @media (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  // .actions-catalog__sort
  &__sort {
    @media (max-width: $md3) {
      margin-bottom: 15px;
    }
  }
  // .actions-catalog__view
  &__view {
  }
}
.sort-by-catalog {
  display: flex;
  align-items: center;
  // .sort-by-catalog__label
  &__label {
    margin-right: 18px;
    font-size: 14px;
    font-weight: 900;
    line-height: 18px;
    color: #333;
  }
  // .sort-by-catalog__select-wrapper
  &__select-wrapper {
    font-size: 14px;
    font-weight: 300;
    text-align: left;
    color: #0097ac;
  }
  // .sort-by-catalog__select
  &__select {
  }

}
.view-catalog {
  display: flex;

  // .view-catalog__item
  &__item {
    width: 36px;
    height: 34px;
    // .view-catalog__item--grid
    &--grid {
      background:  url(../img/icons/catalog/catalog-view-grid.png) 0 0 / auto no-repeat;
      margin-right: 7px;
    }
    // .view-catalog__item--list
    &--list {
      background:  url(../img/icons/catalog/catalog-view-list.png) 0 0 / auto no-repeat;
    }
  }
}
.nav-catalog {
  padding: 10px 17px;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;

  @media (min-width: 768px) {
    display: flex;
    justify-content: space-between;
  }

  // .nav-catalog__show
  &__show {
    @media (max-width: $md3) {
      margin-bottom: 15px;
    }
  }
  // .nav-catalog__pagging
  &__pagging {
  }
}
.show-catalog {
  display: flex;
  align-items: center;
  // .show-catalog__label
  &__label {
    margin-right: 15px;
    font-size: 14px;
    font-weight: 900;
    line-height: 18px;
    text-align: left;
    color: #333;
  }

  // .show-catalog__select-wrapper
  &__select-wrapper {
    font-size: 16px;
    font-weight: 900;
    text-align: left;
    color: #999;

    .select__body {
      width: 56px;
      border: solid 2px #f3f3f3;
    }

    .select__value {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  // .show-catalog__select
  &__select {
  }
}
.pagging-catalog {
  display: flex;
  align-items: center;
  // .pagging-catalog__arrow
  &__arrow {
    display: block;
    width: 33px;
    height: 29px;
    background: url(../img/icons/product-slider/slider-arrow.png) 0 0/auto no-repeat;

    &:first-child {
      transform: rotate(180deg);
    }

    &:last-child {
    }
  }
  // .pagging-catalog__list
  &__list {
    display: flex;
    margin: 0 30px;
    li {
      @media (max-width: $md3) {
        &:last-child {
          display: none;
        }
      }
    }

    @media (max-width: $md3) {
      margin: 0 10px;
    }
  }
  // .pagging-catalog__item
  &__item {
    font-size: 18px;
    font-weight: 900;
    line-height: 24px;
    text-align: left;
    color: #333;
    width: 33px;
    height: 33px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;

    &._active {
      color: #fff;
      background-color: #fd8424;
    }
  }
}












